@import '../../variables.scss';

footer {
  background-color: $main-bg-color;
  padding: $main-padding;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  row-gap: 1rem;
  column-gap: 2rem;
  color: $white-color;

  @media (min-width: $min-width) {
    flex-direction: row;
  }
}

.footer-git-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 1rem;
  row-gap: 0.5rem;
  flex-direction: column;
  align-items: center;

  @media (min-width: $min-width) {
    flex-direction: row;
  }
}

.footer-git-link {
  white-space: nowrap;
  @extend %anchor-default;

  &:hover {
    @extend %anchor-hover;
  }
}

.footer-copyright {
  white-space: nowrap;
}
